import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import Login from '@/views/Login'
import Home from '@/views/Home'
import UserInfo from "@/views/UserInfo";
import UserList from "@/views/UserList";
Vue.use(Router)

export default new Router({

  mode:'history',
  routes: [
    {
      path: '/helloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/login',
      redirect: '/'
    },{
      path: '/logout',
      name: 'Logout',
      redirect: '/'
    },
    {
      path: '/home/:username',
      name: 'Home',
      component: Home,
      children:[
        {
          path: '/user/info',
          name: 'UserInfo',
          component: UserInfo
        },{
          path: '/user/list',
          name: 'user',
          component: UserList
        }
      ]
    }
  ]
})



